import React from 'react'

function Home(props) {
    
    return (
        <div className='page'>
            {props.navbar}
            <br />
            <br />
            <div id='main'>
                <h1 id='banner'>The Resolute Dev</h1>
                <div>{props.posts}</div>
            </div>
        </div>
    )
}

export default Home