import React from 'react'
import picBi from '../images/setState/Day22-bindingStatement.jpg'
import picEH from '../images/setState/Day22-eventHandler.jpg'
import picOC from '../images/setState/Day22-onClick.jpg'
import picPS from '../images/setState/Day22-prevStateParameter.jpg'
import picRe from '../images/setState/Day22-returnStatement.jpg'
import picSS from '../images/setState/Day22-setStateMethod.jpg'
import pic22 from '../images/setState/Day22.jpg'

export default function SetState(props) {
    return (
        <div className='page'>
            {props.navbar}
            <br />
            <br />
            <h1 className='title'>Changing State in React Using setState()</h1>
            <div>Wednesday, January 23rd, 2019 || 10:02AM CST</div>
            
            
            <div class="text-body">
              <h3><strong>Its Confusing, I Know</strong></h3>
                <p>&emsp;Once you learn how to initialize state in React, youre probably feeling pretty cool. Great, youre thinking, 
                state is a key concept in React and Im actually <em>getting it</em>!</p>
                <p>&emsp;And then you get to setting and changing state. There are all these new moving parts that you have to use and 
                its just so frustrating to piece it all together! Whats that binding statement about? Do I need an event handler to set 
                the state? These questions are important and Im going to address them 
                in a way I feel is less technical and morefor lack of better words, human.</p>
              <h3><strong>Cookies</strong></h3>
                <p>&emsp;For this article, I thought it would be a fun and useful analogy to compare a React component to a Cookie Jar, 
                and to compare the state of that Cookie Jar to the jars contents. In my opinion, this analogy works because:</p>
                <ul>
                  <li>The amount of cookies in the jar is subject to change (state can update)</li>
                  <li>You cant just change the amount of cookies without opening the jar first (state is protected from direct changes)</li>
                  <li>When you bake more cookiesor eat someyou dont just go buy a new jar. You change the contents of the existing cookie jar. 
                    (state can be modified indirectly, not reassigned a new object directly)</li>
                  <li>You can choose which kind of cookie you want to add or take (you can update as many or as few properties in state as you want)</li>
                </ul>
                  <br />
                <figure>
                 <img className="img-center" src={pic22} alt='tutorial pic'/>
                 <figcaption>Because who doesnt like cookies?</figcaption>
                </figure>
                  <br />
              <h3><strong>The Event Handler</strong></h3>
               <p>&emsp;The event handler, well, handles the event, whatever it is. It could be a click, a mouseover, 
                a hover, whatever you want the user to do to trigger your change in state. In the cookie jar analogy, this would be the 
                action of unlocking/opening the cookie jar so you can put in or take out cookies (but the actual event is the click, 
                as you can see in the <strong><em>onClick</em></strong> attribute for the /button/ element).</p>
                <figure>
                 <img className="img-center" src={picOC} alt='tutorial pic'/>
                  <figcaption>Remember to wrap your Javascript stuff in curly braces in the render statement!</figcaption>
                </figure>
                  <br />
                <p>&emsp;The <strong><em>handleClick()</em></strong> method is our examples event handler. Youll notice that the setState() method is inside of it. 
                This is standard, so just get used to putting setState() inside of a method every time. Drilling this syntax is
                going to help this become muscle memory for you, so I recommend typing it several times.</p>
                <figure>
                  <img className="img-center" src={picEH} alt='tutorial pic'/>
                  <figcaption>It's Method-ception</figcaption>
                </figure>
                  <br />
               <p>&emsp;This method-within-a-method thing is just a way for React to protect state from accidental direct changes. 
                Its why you cant just assign a new object to state with the assignment operator (=), and its a good thing. It may be roundabout, but 
                its best practice and part of functional programming principles to not alter state directly.</p>
                <br />
              <h3><strong>Binding Statement</strong></h3>
                <p>&emsp;Dont forget the binding statement! This is what binds your <strong><em>handleClick()</em></strong> method to your CookieJar component. Why do we have 
                to bind it? Because it lets you use the method anywhere in the component. In the cookie jar analogy, its like getting 
                permission to open the cookie jar in the first place. Without the binding statement, 
                youre not going to be able to get into that jar, no 
                matter how good those cookies look (maybe using this example was a mistake, Im getting hungry).</p>
                 <br />
                <figure>
                  <img className="img-center" src={picBi} alt='tutorial pic'/>
                  <figcaption>this just refers to the CookieJar components scope.</figcaption>
                </figure>
                  <br />
                <p>&emsp;Just make sure you put the binding statement in the constructor function (usually right after 
                you initialize state), and put the method definition between 
                the constructor function and the render method. Location matters.</p>
                <br />
              <h3><strong>setState() Method</strong></h3>
                <p>&emsp;This is where the magic happens. Maybe you baked a fresh batch of cookies and need to add them 
                to the jar, or maybe youre going to sneak one or two in the middle of the night 
                while your roommates asleep. Either way, this is where you decide how state is going to change.</p>
                <p>&emsp;In our example, we just baked a pan of 12 snickerdoodles, and we want to add 
                them to the jar every time we click that button (see the render() method in the full example).</p>
                  <br />
               <figure>
                  <img className="img-center" src={picSS} alt='tutorial pic'/>
                 <figcaption>Cookies and React: a match made in oven.</figcaption>
               </figure>
                  <br />
               <p>&emsp;In order to know how many snickerdoodles well have once the new ones are added, 
                we need to know how many snickerdoodles were already in the jar to begin with. Thats where the next topic comes in.</p>
                <br />
              <h3><strong>Current/Previous State Object Parameter</strong></h3>
                <p>&emsp;We need to know what the cookie jar looks like right now (present) before we change it (future).
                Or we can put it another way, that we need to know what it LOOKED like (past) before we MADE changes to it (present). Either way, 
                the basic idea is the same: state before and state after. 
                This parameter <em>oldCookies</em> is what well call <strong>the before</strong>.</p>
                  <br />
                <figure>
                  <img className="img-center" src={picPS} alt='tutorial pic' />
                  <figcaption>This picture was taken in 2019 B.C. (before cookies)</figcaption>
                </figure>
                  <br />
               <p>&emsp;You might see this parameter named <em>prevState</em> in other peoples code, and thats fine. 
                Just remember its important to include this parameter in your setState() method because without it, 
                youre throwing cookies into the jar blind. More importantly, React wont let 
                you change anything without this parameter because it wants to refer to the old states properties to form the new state.</p>
                  <br />
              <h3><strong>Return statement in setState()</strong></h3>
                <p>&emsp;This is the last bit that I know I had trouble understanding at first. Think of it as <strong>the after</strong>. 
                Youve referred to <strong>the before</strong> already, and now youre defining the appearance of state after your 
                changes have been made. Its the new state object, but 
                you dont have to list every property from the old object, because React keeps those the 
                same for you unless you change them here.</p>
                  <br />
                <img className="img-center" src={picRe} alt='tutorial pic'/>
                  <br />
                <p>&emsp;We only baked snickerdoodles this time, so were only going to add 12 to 
                snickerdoodles. The rest of the cookies are still in the jar. 
                Were just not modifying them, so they dont need to be mentioned in the <strong>return statement</strong>.</p>
              <h3><strong>Recap</strong></h3>
                <p><em>Event handler</em>unlocks the cookie jar (accesses state)</p>
                <p><em>Binding statement</em>gives you permission to open the cookie jar (allows access to state)</p>
                <p><em>setState() method</em>changes whats in the cookie jar (updates state based on 
                  changes you make in the return statement)</p>
                <p><em>Current/Previous state object parameter</em>the cookie jar before you make your changes 
                  (state before your changes are made; you have to refer to it to make the new state)</p>
                <p><em>Return statement in setState()</em>the cookies youre adding, removing, or otherwise changing 
                  (the new look of the state object; your changes to state)</p>
                <p>Let me know if this analogy helped in the comments below! Thanks for reading ^_^</p>
            </div>
        </div>
    )
}