import React from "react";

function Navbar(props) {
  return (
    <nav className="nav">
      <a id='home' href='https://boutchersj.github.io/theResoluteDev' className="non-dropdown menu-link">
        Home
      </a>
      <div className="dropdown">
        <div className="menu-link" id="posts-dropdown" onClick={props.toggleDropdown}>
          Posts
        </div>
        <div id="posts-list" className='hide'>
          <div id="newYear" onClick={props.changePage}>
            New Year, New Blog!
          </div>
          <div id="setState" onClick={props.changePage}>
            Changing State in React Using setState()
          </div>
          <div id="createReactApp" onClick={props.changePage}>
            Deploy Your React App to GitHub Pages!
          </div>
          <div id="neverGiveUp" onClick={props.changePage}>
            This is the Most Underrated Developer Tool
          </div>
        </div>
      </div>
      <a id="portfolio" href='https://boutchersj.github.io' className="non-dropdown menu-link">
        Portfolio Site
      </a>
    </nav>
  );
}

export default Navbar;

