import React from 'react';
import '../styles/Blog.css';
import Post from './Post'
import data from '../postData.json'
import Navbar from './Navbar'
import Home from './Home'
import NewYear from './NewYear'
import SetState from './SetState'
import CreateReactApp from './CreateReactApp'
import NeverGiveUp from './NeverGiveUp';

class Blog extends React.Component {
  constructor(){
    super()
    this.state = {
      newYear: false,
      newYearPost: false,
      setState: false,
      setStatePost: false,
      createReactApp: false,
      createReactAppPost: false,
      neverGiveUp: false,
      neverGiveUpPost: false
    }
    this.changePage = this.changePage.bind(this)
    this.toggleDropdown = this.toggleDropdown.bind(this)
  }
  changePage(event) {
    const { id } = event.target;
    let posts = ['newYear', 'newYearPost', 'setState', 'setStatePost', 'createReactApp', 'createReactAppPost', 'neverGiveUp', 'neverGiveUpPost'];
    posts.splice(posts.indexOf(id), 1);
    const other1 = posts[0];
    const other2 = posts[1];
    const other3 = posts[2];
    const other4 = posts[3];
    const other5 = posts[4];
    const other6 = posts[5];
    const other7 = posts[6];
    this.setState({
      [id]: true,
      [other1]: false,
      [other2]: false,
      [other3]: false,
      [other4]: false,
      [other5]: false,
      [other6]: false,
      [other7]: false
    });

  }
  toggleDropdown(event) {
    const { id } = event.target;
    const content = document.getElementById(id).nextSibling
    content.className = content.className === 'hide' ? 'show' : 'hide'
    console.log('ClassName = ' + content.className)
  }
  render(){
    const navbar = (
      <Navbar changePage={this.changePage} toggleDropdown={this.toggleDropdown} />
    )
    const posts = data.posts.map((post) => {
      return <Post
          id={post.id}
          title = {post.title}
          date = {post.date}
          preview = {post.preview}
          changePage = {this.changePage}
      />
  })
    return this.state.newYear === true || this.state.newYearPost === true ? (
      <NewYear 
        navbar={navbar}
      />
    ) : this.state.setState === true || this.state.setStatePost === true ? (
      <SetState 
        navbar={navbar}
      />
    ) : this.state.createReactApp === true || this.state.createReactAppPost === true ? (
      <CreateReactApp
        navbar={navbar}
      />
    ) : this.state.neverGiveUp === true || this.state.neverGiveUpPost === true ? (
      <NeverGiveUp
        navbar={navbar}
      />
    ) :
      <Home navbar={navbar} posts={posts} />
  }
}

export default Blog;
