import React from 'react'

export default function NewYear(props) {
    return (
        <div className='page'>
            {props.navbar}
            <br />
            <br />
            <h1 className='title'>New Year, New Blog!</h1>
            <div>Sunday, January 6th, 2019 || 11:58PM CST</div>

            <div class="text-body">
                <p>&emsp;Hello World! Welcome to my all new blog for 2019. This is an exciting time. Let me start by introducing myself. I'm 
                    Steven Boutcher, I recently graduated with a Spanish teaching degree, and I've been learning to code since December 5th, 
                    2017. Aside from coding, my greatest passion is 
                    <a rel='noopener noreferrer' href="https://parkourgenerations.com/le-parkour-overview/" target="_blank">
                     parkour </a> and it's probably the only thing keeping the pizza and ice cream from winning.
                </p>
                <p>&emsp;My main focus when learning to code has been on web development, so things like HTML, CSS, Javascript, all those 
                    fun libraries like Bootstrap and jQuery, frameworks like React.js...and I wouldn't have gotten this far without 
                     <a rel='noopener noreferrer' href="https://www.freecodecamp.org/" target="_blank"> freeCodeCamp </a>, so definitely give them a try if you're struggling to 
                    learn these things I've mentioned above!
                </p>
                <p>&emsp;Before this blog, I had another one that I used as a way to motivate myself to eat healthy, exercise more, and code daily. 
                    It was an accountability system, but it was really just a training diary, not a blog.
                </p>
                <p>&emsp;I want this blog to be about paying it forward to all you fellow students out there. I'll admit I haven't found a 
                    job in web development yet, and I still have much more to learn before I break into the field. I hope this blog will give you 
                    as much insight as it gives me. I look forward to coding with you in 2019!</p>
                <p>"In learning you will teach, and in teaching you will learn.</p>
                <p> Phil Collins</p> 
            </div>
        </div>
    )
}